import { useState } from "react";
import style from "../../Income/Income.module.css";
import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import CustomSelect from "../../../../Ui/Select/CustomSelect";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../../Utils/Functions/dateUtility";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useFinanceOtherFeeMiscellaneous } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";

const OneBankMiscellaneousDaybookFilter = ({ onClose, bankId, fid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    from: "",
    to: "",
    payment_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [financeOtherFeeMiscellaneous] = useFinanceOtherFeeMiscellaneous();

  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };
  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      payment_type: val,
    }));
  };
  const onFilter = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && fid && bankId) {
      setDisabled((pre) => !pre);
      financeOtherFeeMiscellaneous({
        fid: fid,
        bodyParameter: {
          bank: bankId,
          from: state.from,
          to: state.to,
          payment_type: state.payment_type,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("filter_daybook")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormCalendarInput
              label={t("from")}
              placeholder={t("please_select_from_date")}
              name={"from"}
              value={state.from ? slashDateFormat(state.from) : ""}
              onChange={(e) => onTransactionDate(e, "from")}
              errorShow={errorField["from"]}
            />
            <FormCalendarInput
              label={t("to_only")}
              placeholder={t("please_select_to_date")}
              name={"to"}
              value={state.to ? slashDateFormat(state.to) : ""}
              onChange={(e) => onTransactionDate(e, "to")}
              errorShow={errorField["to"]}
            />
            <CustomSelect
              selectLabel={t("payment_type")}
              selectedValue={t("please_select_payment_type")}
              options={["Payment Gateway - PG", "By Cash"]}
              onClick={onSelectType}
              defalutValue={state.payment_type}
              errorShow={errorField["payment_type"]}
            />
            <CreateButton label="filter" onAction={onFilter} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default OneBankMiscellaneousDaybookFilter;
