import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { cdnUrl, pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import style from "../../FinanceExport/FinanceExport.module.css";
import dayjs from "dayjs";
import OneBankDaybookCardMenu from "./OneBankDaybookCardMenu";
import { useState } from "react";
// import ShowingGeneratedPdf from "../../../../Ui/GeneratedPdf/ShowingGeneratedPdf";

const OneBankDaybookCard = ({ excel, bankId, onRefetch }) => {
  const [xlsxId, setXlsxId] = useState("");
  const [openPdf, setOpenPdf] = useState(false);
  const onClose = () => {
    setXlsxId("");
  };
  const onOpenPdf = () => {
    setOpenPdf((pre) => !pre);
  };
  const onMenu = (e) => {
    e.stopPropogation();
    // e.preventDefault();
    setXlsxId(excel);
  };
  return (
    <>
      <a
        key={excel?._id}
        className={style.export_card}
        title="Download File"
        href={`${cdnUrl}/${excel?.excel_file}`}
        target="_blank"
        rel="noopener noreferrer"
      >
        {/* <div className={style.export_card} onClick={onOpenPdf}> */}
        <img
          src={`${assestsUserFinanceUrl}/xsxl.svg`}
          className={style.export_card_image}
          alt="excel icon"
        />
        <section className={style.export_card_text}>
          <h6>{excel?.excel_file_name ?? ""}</h6>
          <div className={style.export_card_date}>
            <h6>{excel?.bank?.finance_bank_account_name ?? ""}</h6>
            <h6>
              {dayjs(excel?.created_at).format("DD MMM YYYY")},{" "}
              {localTimeFormat(excel?.created_at, "LT")}
            </h6>
          </div>
          <div className={style.export_card_date}>
            <h6>
              From : {excel?.from} To {excel?.to}
            </h6>
            <h6>
              {excel?.payment_type === "BOTH"
                ? "Total"
                : excel?.payment_type === "CASH_BANK"
                ? "Cash / Bank"
                : excel?.payment_type}
            </h6>
          </div>
        </section>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
          title="Menu"
          className={style.export_card_menu}
        />
        {/* </div> */}
      </a>
      {xlsxId && (
        <OneBankDaybookCardMenu
          onClose={onClose}
          xlsxId={xlsxId}
          bankId={bankId}
          onRefetch={onRefetch}
        />
      )}

      {/* {openPdf && (
        <ShowingGeneratedPdf
          subUrl={pdfCdnUrl}
          // subUrl={`https://qviple.com/api/v1/all-images`}
          documentUrl={excel?.excel_file}
          fileName={excel?.excel_file_name}
          onClose={onOpenPdf}
        />
      )} */}
    </>
  );
};

export default OneBankDaybookCard;
