import style from "../../FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useCallback, useState } from "react";
import OneBankFund from "./OneBankFund";
import OneBankInfo from "./OneBankInfo";
import OneBankMenu from "./OneBankMenu";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useFinanceOneBankDetail } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import {
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import OneBankDaybook from "./OneBankDaybook";
import OneBankMiscellaneousDaybook from "./OneBankMiscellaneousDaybook";

const OneBank = ({ fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const [add, setAdd] = useState(false);
  const [bankEdit, setBankEdit] = useState(false);

  const { financeOneBankDetail } = useFinanceOneBankDetail({
    bid: getQuery.state.bankId,
    skip: !getQuery.state.bankId,
  });

  const onToggle = useCallback(() => {
    setAdd((pre) => !pre);
  }, []);

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance`
        : `/q/${params.username}/member/staff/finance`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  //   console.info("financeOneBankDetail", financeOneBankDetail);
  return (
    <>
      {!bankEdit && (
        <>
          <UserMemberContentWrapper>
            <div className={style.fm_header_container}>
              <div
                className={style.fm_header_start_container}
                style={{
                  gap: "0.2rem",
                }}
              >
                <img
                  alt="search icon"
                  src={`${assestsUtilityUrl}/back.svg`}
                  style={{
                    height: "2rem",
                    width: "2rem",
                  }}
                  onClick={onNavigate}
                />
                <h6>{t("bank_details")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/edit.svg`}
                alt="back arrow"
                title="Edit Bank"
                onClick={onToggle}
              />
            </div>
            <HorizontalTabWrapper>
              {tabList.map((tab) => (
                <HorizontalTabCard
                  key={tab?.id}
                  tab={tab}
                  isLink
                  isActive={tab.groupIs?.includes(searchParams.get("d"))}
                  parentState={getQuery.state}
                  url={
                    getQuery.state?.urlOffSet === "Institute"
                      ? `/q/${params.username}/institute/member/finance?type=bank&n=detail${tab.urlKey}`
                      : `/q/${params.username}/member/staff/finance?type=bank&n=detail${tab.urlKey}`
                  }
                />
              ))}
            </HorizontalTabWrapper>
            <BorderBottom
              customStyleBorder={{
                marginTop: "0",
              }}
            />
          </UserMemberContentWrapper>

          <UserMemberContentWrapper>
            {searchParams.get("d") === "info" ? (
              <OneBankInfo oneBank={financeOneBankDetail?.one_bank} />
            ) : searchParams.get("d") === "fund" ? (
              <OneBankFund
                totalRepay={financeOneBankDetail?.one_bank?.total_repay}
                collectOnline={financeOneBankDetail?.one_bank?.collect_online}
                dueRepay={financeOneBankDetail?.one_bank?.due_repay}
              />
            ) : searchParams.get("d") === "miscellaneous" ? (
              <OneBankMiscellaneousDaybook
                fid={fid}
                bankId={getQuery.state.bankId}
              />
            ) : (
              <OneBankDaybook fid={fid} bankId={getQuery.state.bankId} />
            )}
          </UserMemberContentWrapper>
        </>
      )}
      {add && (
        <OneBankMenu
          onToggle={onToggle}
          setBankEdit={setBankEdit}
          openBankMenu={financeOneBankDetail?.one_bank}
        />
      )}
    </>
  );
};

export default OneBank;

const tabList = [
  {
    label: "admission_daybook",
    id: "admission",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "miscellaneous_daybook",
    id: "miscellaneous",
    urlKey: "&d=miscellaneous",
    groupIs: ["miscellaneous"],
  },
  {
    label: "bank_info",
    id: "info",
    urlKey: "&d=info",
    groupIs: ["info"],
  },
  {
    label: "fund",
    id: "fund",
    urlKey: "&d=fund",
    groupIs: ["fund"],
  },
];
